import React, { useEffect, useRef, useState } from "react";
import Input from "./Input";
import classes from "./AddTransactionForm.module.css";
import ExpenseType from "./ExpenseType";
import Button from "./Button";
import validateForm from "../custom-Hooks-Helper-Function/ValidateForm";
import useExpenseTypeData from "../custom-Hooks-Helper-Function/ExpenseTypeData";
const AddTransactionForm = (props) => {
  const editTransactionData = props.editTransaction || {};
  const expenseTypeArr = useExpenseTypeData();
  const validate = validateForm();
  const [currSelectedExpenseType, setCurrSelectedExpenseType] = useState(
    editTransactionData.expenseType || 6
  );
  const [errorArr, setErrorArr] = useState([]);
  const [transactionType, setTransactionType] = useState(
    editTransactionData.transactionType || "expense"
  );
  const [formIsReady, setFormIsReady] = useState(false);

  // input refs
  const amountRef = useRef();
  const descriptionRef = useRef();
  const dateRef = useRef();
  const transactionTypeRef = useRef();

  // check for validation after form submit button click
  const formSubmitHandler = () => {
    let formData = [amountRef.current.value, descriptionRef.current.value];
    setErrorArr(validate(formData, 'addtransactionform'));
    setFormIsReady(true);
  };

  // transaction type change handler
  const transactionTypeChangeHandler = (e) => {
    setTransactionType(e.target.value);
  };
  
  // set form data if no error.
  useEffect(() => {
    if (formIsReady && !errorArr[0] && !errorArr[1]) {
      props.submitFormData({
        id: editTransactionData.id || (Math.random() * 100).toFixed(3),
        title:
          transactionType == "income"
            ? "Income"
            : expenseTypeArr[currSelectedExpenseType - 1].text,
        transactionType,
        amount: amountRef.current.value,
        date: dateRef.current.value || new Date().toJSON().slice(0, 10),
        description: descriptionRef.current.value,
        expenseType:
          transactionType == "income" ? null : currSelectedExpenseType,
      });
    }
  }, [errorArr, formIsReady]);

  return (
    <div className={classes.addtransactionform__container}>
      <h3>{editTransactionData.date ? "Edit" : "Add"} Transaction</h3>
      <div className={classes.row__1}>
        <div>
          <label>Select Type</label>
          <select
            ref={transactionTypeRef}
            onChange={transactionTypeChangeHandler}
            defaultValue={editTransactionData.transactionType}
          >
            <option value="expense">Expense</option>
            <option value="income">Income</option>
          </select>
        </div>
        <Input
          type="number"
          labelText="Enter Amount"
          forwardRef={amountRef}
          errorMassage={errorArr[0]}
          defaultValue={editTransactionData.amount}
        ></Input>
      </div>
      <div className={classes.row__2}>
        <Input
          type="text"
          labelText="Add Description"
          forwardRef={descriptionRef}
          errorMassage={errorArr[1]}
          defaultValue={editTransactionData.description}
        ></Input>
      </div>
      <div className={classes.row__3}>
        <Input
          type="date"
          labelText="Select Date"
          forwardRef={dateRef}
          defaultValue={editTransactionData.date}
        ></Input>
      </div>
      {transactionType == "expense" && (
        <div className={classes.row__4}>
          <h5>Select Expense Type</h5>
          <div className={classes.expense__types__list__container}>
            {expenseTypeArr.map((expenseTypeData, ind) => (
              <ExpenseType
                key={ind}
                id={ind}
                text={expenseTypeData.text}
                imageSrc={expenseTypeData.imageSrc}
                setSelectedExpenseType={setCurrSelectedExpenseType}
                currSelected={currSelectedExpenseType}
              ></ExpenseType>
            ))}
          </div>
        </div>
      )}
      <div className={classes.row__5}>
        <Button
          buttonText={
            editTransactionData.date ? "Edit Transaction" : "Add Transaction"
          }
          isEnabled={true}
          handleClick={formSubmitHandler}
        ></Button>
      </div>
    </div>
  );
};

export default AddTransactionForm;
