import { createSlice } from "@reduxjs/toolkit";

let initialState = {
  user: { userName: null, email: null },
  userSettings: {
    currency: null,
    image:null
  },
  token: null,
};

const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    setUser(state, action) {
      const userData = action.payload.userData;
      const authToken = action.payload.token;
      state.user = userData;
      state.token = authToken;
      localStorage.setItem("token", JSON.stringify(authToken));
      localStorage.setItem('user',JSON.stringify(userData))
    },
    setUserSettings(state, action) {
      const updateData = {currency:action.payload.currency,image:action.payload.image};
      if(action.payload.name){
        state.user.userName = action.payload.name;
        const userData = JSON.parse(localStorage.getItem('user'));
        userData.name=action.payload.name;
        localStorage.setItem('user',JSON.stringify(userData))
      }
      state.userSettings = updateData;
      localStorage.setItem('userSettings',JSON.stringify(action.payload))
    },
    clearUserData(state, action) {
      localStorage.clear();
    },
  },
});


export const userActions = userSlice.actions;

export default userSlice.reducer;
