import React from "react";
import { useSelector } from "react-redux";
import DashBoard from "../../components/DashBoard";
import Header from "../../components/Header";
import IncomeOrExpenseListContainer from "../../components/IncomeOrExpenseListContainer";

const Home = () => {
  let totalExpense = 0;
  let totalIncome = 0;
  const transactionList = useSelector(
    (state) => state.transaction.transactionList
  );

  const expenseTransactionList = transactionList.filter((transaction) => {
    if (transaction.transactionType == "expense") {
      return (totalExpense += parseInt(transaction.amount));
    }
  });
  const incomeTransactionList = transactionList.filter((transaction) => {
    if (transaction.transactionType == "income") {
      return (totalIncome +=parseInt(transaction.amount));
    }
  });
  return (
    <div>
      <DashBoard totalExpense={totalExpense} totalIncome={totalIncome} totalAmountLeft={totalIncome-totalExpense}></DashBoard>
      {/* income list */}
      <IncomeOrExpenseListContainer
        list={incomeTransactionList}
        title="Incomes"
      ></IncomeOrExpenseListContainer>
      {/* expense list */}
      <IncomeOrExpenseListContainer
        list={expenseTransactionList}
        title="Expenses"
      ></IncomeOrExpenseListContainer>
    </div>
  );
};

export default Home;
