import React from "react";
import classes from "./DashBoard.module.css";
import greenArrow from "../img/green-arrow.png";
import redArrow from "../img/red-arrow.png";
import { useSelector } from "react-redux";
const DashBoard = ({totalIncome,totalExpense,totalAmountLeft}) => {
  const {userSettings} = useSelector(state=>state.user);
  let currencySymbol = userSettings.currency == 'rupee' ? '' : '$';
  return (
    <div className={classes.dashboard__container}>
      <div>
        <h3>DashBoard</h3>
        <div className={classes.total__balance__show__container}>
          <p className={classes.total__balance__text}>Total Balance</p>
          <p className={classes.total__balance}>{currencySymbol}{totalAmountLeft}.00</p>
        </div>
        <div className={classes.income__expense__container}>
          <div className={classes.income__container}>
            <div className={classes.arrow__image__container}>
              <img src={greenArrow}></img>
            </div>

            <p>Total Income</p>
            <span>{currencySymbol}{totalIncome}.00</span>
          </div>
          <div className={classes.expense__container}>
            <div className={classes.arrow__image__container}>
              <img src={redArrow} className={classes.rotate__img}></img>
            </div>
            <p>Total Expense</p>
            <span>{currencySymbol}{totalExpense}.00</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DashBoard;
