import React, { useReducer } from "react";
import Chart from "./Chart";
import classes from "./Visualize.module.css";
import { useSelector } from "react-redux";
import expenseTypeData from "../../custom-Hooks-Helper-Function/ExpenseTypeData";
const initialState = { select1: "income", select2: "Month" };
import BarChartView from "./BarChart";
const reducer = (state, action) => {
  switch (action) {
    case "income":
      return { select1: "income", select2: state.select2 };
    case "expense":
      return { select1: "expense", select2: state.select2 };
    case "Month":
      return { select1: state.select1, select2: "Month" };
    case "Types":
      return { select1: state.select1, select2: "types" };
  }
};

const monthArr = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "Jun",
  "Jul",
  "Aug",
  "Sep",
  "Oct",
  "Nov",
  "Dec",
];

const Visualize = () => {
  const [selected, dispatch] = useReducer(reducer, initialState);
  const expenseData = expenseTypeData();
  const transactionList = useSelector(
    (state) => state.transaction.transactionList
  );
  const incomeTransactionList = transactionList.filter(
    (transaction) => transaction.transactionType == "income"
  );
  const expenseTransactionList = transactionList.filter(
    (transaction) => transaction.transactionType == "expense"
  );

  const selectChangeHandler = (e) => {
    dispatch(e.target.value);
  };

  const select2ChangeHandler = (e) => {
    dispatch(e.target.value);
  };

  let data = [];

  // prepare the data for chart to show details of different expense type trnsactions.
  if (selected.select2 == "types" && selected.select1 == "expense") {
    const sortedExpenseList = expenseTransactionList.sort(
      (a, b) => a.expenseType - b.expenseType
    );
    let numberOfExpenseArr = [];
    for (let i = 1; i < 7; i++) {
      let n = sortedExpenseList.filter(
        (transaction) => transaction.expenseType == i
      );
      numberOfExpenseArr.push(n.length);
    }
    for (let i = 0; i < expenseData.length; i++) {
      if (numberOfExpenseArr[i]) {
        data.push({ name: expenseData[i].text, value: numberOfExpenseArr[i] });
      }
    }
  }

  // preapre the data for chartbar to display no of transactions per month and total transaction amount.

  if (selected.select2 == "Month") {
    // sorting the list according to month;
    function calcMonth(list) {
      return list.sort((a, b) => new Date(a.date) - new Date(b.date));
    }
    const sortedByMonthList =
      selected.select1 == "income"
        ? calcMonth(incomeTransactionList)
        : calcMonth(expenseTransactionList);

    //after sorting calculate the number of transactions and total transaction amount.

    for (let i = 0; i < 12; i++) {
      let sum = 0;
      let n = sortedByMonthList.filter((transaction) => {
        const date = new Date(transaction.date);
        if (date.getMonth() == i) {
          sum += transaction.amount;
          return transaction;
        }
      });

      data.push({ name: monthArr[i], no: n.length, amt: sum });
    }
  }

  return (
    <div className={classes.visualize__page__container}>
      <h3>Visualize</h3>
      <div className={classes.action__container}>
        <select onChange={selectChangeHandler}>
          <option value="income">Income</option>
          <option value="expense">Expense</option>
        </select>
        <select onChange={select2ChangeHandler}>
          <option value="Month">Month</option>
          {selected.select1 == "expense" && <option>Types</option>}
        </select>
      </div>
      {selected.select2 == "types" && <Chart data={data}></Chart>}
      {selected.select2 == "Month" && (
        <div className={classes.barchart__container}>
          <BarChartView data={data}></BarChartView>
        </div>
      )}
    </div>
  );
};

export default Visualize;
