import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import AddTransactionForm from "../../components/AddTransactionForm";
import transactionActionsToDatabase from "../../store/TransactionActions";

const EditTransaction = () => {
  const tranActions = transactionActionsToDatabase()
  const { transactionList } = useSelector((state) => state.transaction);
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const id = params.get("id");
  const transactionArr = transactionList.filter(
    (transaction) => transaction.id == id
  );

  const { transactionType, amount, description, expenseType, date } =
    transactionArr[0];
  const [formData, setFormData] = useState(null);
 


  useEffect(() => {
    if (formData) {
      tranActions.editTransaction(formData);
    }
  }, [formData]);

  return (
    <>
      <AddTransactionForm
        editTransaction={{
          transactionType,
          date,
          amount,
          description,
          expenseType,
          id,
        }}
        submitFormData={setFormData}
      ></AddTransactionForm>
    </>
  );
};

export default EditTransaction;
