import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import useSendHttpRequest from "../custom-Hooks-Helper-Function/useSendHttpRequest";
import { userActions } from "./userSlice";

const userActionsToDatabase = ()=>{
    const dispatch = useDispatch();
    const navigate = useNavigate()
    const [reqData,setReqData] = useState({url:null,optionObject:null,auth:null,message:''});
    const [action,setAction] = useState(null);     
    const data = useSendHttpRequest(reqData);
     
    useEffect(()=>{
         if(data){
             if(action == 'updatesetting'){
                 dispatch(userActions.setUserSettings(data.data))
             }
             if(action == 'authenticate'){
                dispatch(userActions.setUserSettings(data.userSetting));
                dispatch(
                  userActions.setUser({ userData: data.userData, token: data.token })
                );
                navigate('/home')
             }
         }
    },[data,action])

    return {
       authenticateUser:(authData)=>{
         const url = `/authenticate/${authData.type}`;
         const optionObject = {
            method: "POST",
            body: JSON.stringify(authData.data),
            headers: {
              "Content-Type": "application/json",
            },
          };
        setReqData({url,optionObject,auth:false,message:'Authenticating User...'});
        setAction('authenticate')
       },
       updateSetting:(settingdata)=>{
        const url = "/updatesetting";
        const optionObject = {
             method: "POST",
             body: settingdata,
             headers:{
             }
        };
        setReqData({url,optionObject,auth:true,message:'Updating Setting...'})
        setAction('updatesetting')
       }
    }
};

export default userActionsToDatabase