import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Navigate } from "react-router-dom";
import Header from "../components/Header";
import { userActions } from "../store/userSlice";

const CheckUserExist = (props) => {
  const dispatch = useDispatch();
  const userData = useSelector((state) => state.user);

  // get the data from localstorage.
  let authToken = localStorage.getItem("token");
  let userSettings = localStorage.getItem("userSettings");
  let user = localStorage.getItem("user");

  useEffect(() => {
    // when user refresh the page if data in localstorage is available then set the user.
    if (userData.token == null && authToken) {
      dispatch(
        userActions.setUser({
          userData: JSON.parse(user),
          token: JSON.parse(authToken),
        })
      );
      dispatch(userActions.setUserSettings(JSON.parse(userSettings)));
    }
  }, []);

  if (!authToken && props.urlPath != "/" && props.urlPath != "/loginorsignup") {
    return <Navigate to="/"></Navigate>;
  } else if (
    authToken &&
    (props.urlPath == "/" || props.urlPath == "/loginorsignup")
  ) {
    return <Navigate to="/home"></Navigate>;
  }

  return (
    <div>
      {props.urlPath != "/" && props.urlPath != "/loginorsignup" && <Header></Header>}
      {props.children}
    </div>
  );
};

export default CheckUserExist;
