import { createSlice } from "@reduxjs/toolkit";
const initialState = {
  transactionList: [],
};
const transactionSlice = createSlice({
  name: "transaction",
  initialState,
  reducers: {
    addNewTransaction(state, action) {
      state.transactionList.push(action.payload);
    },
    deleteTransaction(state, action) {
      let id = action.payload.transactionId;
      state.transactionList = state.transactionList.filter(
        (transaction) => transaction.id != id
      );
    },
    editTransaction(state, action) {
      let id = action.payload.id;
      for (let i = 0; i < state.transactionList.length; i++) {
        if (state.transactionList[i].id == id) {
          state.transactionList[i] = action.payload;
          break;
        }
      }
    },
    clearAllTransaction(state, action) {
      state.transactionList = [];
    },
  },
});



export default transactionSlice.reducer;

export const transactionActions = transactionSlice.actions;
