import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { uiActions } from "../store/uiSlice";

const useSendHttpRequest = ({ url, optionObject, auth,message }) => {
  const [data, setData] = useState(null);
  const userData = useSelector((state) => state.user);
  const dispatch = useDispatch();

  // function to fetch the data.
  const fetchData = async (url, optionObject) => {
    dispatch(uiActions.setShowModal({message}));
    try{
      const response = await fetch(url, optionObject);
      const data = await response.json();
      if (!response.ok) {
        alert(data.msg);
        dispatch(uiActions.setShowModal({message:null}))
        return null;
      }
      dispatch(uiActions.setShowModal({message:null}))
      return setData(data);
    }
    catch(e){
      alert(e)
      dispatch(uiActions.setShowModal({message:null}))
    }
   
  };

  // if auth then set authorization.
  if (auth) {
    optionObject.headers.authorization = `Bearer ${userData.token}`;
  }

  useEffect(() => {
    if ((url, optionObject)) {
      fetchData(url, optionObject);
    }
  },[url,optionObject]);

  return data;
};

export default useSendHttpRequest;
