import React from "react";
import { useSelector } from "react-redux";
import classes from "./ShowMassageModal.module.css";
const ShowMassageModal = (props) => {
  const uiData = useSelector((state) => state.ui);
  return (
    <div>
      {uiData.showModal && (
        <div className={classes.modal__container}>
          <p>{uiData.message}</p>
        </div>
      )}
    </div>
  );
};

export default ShowMassageModal;
