// error types for loginorsignup form.
const loginorsignupErrorList = [
  {
    checkForLengthError: {
      minLen: 3,
      maxLen: 6,
      errorMassage: "Enter Characters Between 3 and 6 !",
    },
  },
  { checkContainChar: { char: "@", errorMassage: "Enter Valid Email !" } },
  {
    checkForLengthError: {
      minLen: 6,
      maxLen: 9,
      errorMassage: "Enter Password Between length of 6 and 9 !",
    },
  },
];

// error type for addtransaction form.
const addTransactionErrorList = [
  {
    checkIfInRange: {
      minLimit: 1,
      maxLimit: null,
      errorMassage: "Enter Valid Amount !",
    },
  },
  {
    checkForLengthError: {
      minLen: 2,
      maxLen: 30,
      errorMassage: "Enter Description of Length 2 to 30",
    },
  },
];

const validateForm = () => {
  // check if string length is in range.
  const checkLength = (value, greateThan, lesserThan) => {
    let lengthError = false;
    if (greateThan) {
      lengthError = value > greateThan ? false : true;
    }
    if (lesserThan) {
      lengthError = value < lesserThan ? lengthError || false : true;
    }
    return lengthError;
  };

  // check if string contain a character.
  const checkIfContainChar = (value, char) => {
    return value.includes(char) ? true : false;
  };

  // check if a valie is in range.
  const checkInRange = (value, minLimit, maxLimit) => {
    if (value < minLimit) {
      return false;
    }
    return true;
  };

  return (formDataArr, formType) => {
    let errorArr = [];
    let checkErrorListArr = [];
    // depends on formtype errorlist is set for dirrerent form.
    if (formType == "loginorsignupform") {
      checkErrorListArr = loginorsignupErrorList;
    } else {
      checkErrorListArr = addTransactionErrorList;
    }

    // for each form data run validation defined in checkforErrorListArr for that data.
    formDataArr.forEach((data, ind) => {
      if (data != null) {
        let checkForErrorList = checkErrorListArr[ind];
        let { checkForLengthError, checkContainChar, checkIfInRange } =
          checkForErrorList;
        if (checkForLengthError) {
          let ifLengthError = checkLength(
            data.length,
            checkForLengthError.minLen - 1,
            checkForLengthError.maxLen + 1
          );

          ifLengthError
            ? (errorArr[ind] = checkForLengthError.errorMassage)
            : (errorArr[ind] = null);
        }

        if (checkContainChar) {
          let ifContainChar = checkIfContainChar(data, checkContainChar.char);
          ifContainChar
            ? (errorArr[ind] = null)
            : (errorArr[ind] = checkContainChar.errorMassage);
        }

        if (checkIfInRange) {
          let ifInRange = checkInRange(data, checkIfInRange.minLimit);
          ifInRange
            ? (errorArr[ind] = null)
            : (errorArr[ind] = checkIfInRange.errorMassage);
        }
      }
    });
    return errorArr;
  };
};

export default validateForm;
