import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { transactionActions } from "../store/transactionSlice";
import useSendHttpRequest from "../custom-Hooks-Helper-Function/useSendHttpRequest";
import { useEffect, useState } from "react";
const optionObject = {
  method: "",
  headers: {
    "Content-Type": "application/json",
  },
};
const transactionActionsToDatabase = () => {
  const [action, setAction] = useState(null);
  const [reqData, setReqData] = useState({
    url: null,
    optionObject: null,
    auth: null,
  });

  const data = useSendHttpRequest(reqData);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  useEffect(() => {
    if (data) {
      navigate("/home");
      if (action == "addtransaction") {
        const transaction = data.transaction;
        transaction.id = transaction._id.toString();
        delete transaction._id;
        dispatch(transactionActions.addNewTransaction(transaction));
      } else if (action == "edittransaction") {
        const transaction = data.transaction;
        transaction.id = transaction._id.toString();
        delete transaction._id;
        dispatch(transactionActions.editTransaction(transaction));
      } else if (action == "deletetransaction") {
        const transaction = data.transaction;
        const id = transaction._id.toString();
        dispatch(transactionActions.deleteTransaction({ transactionId: id }));
      } else if (action == "deletealltransaction") {
        dispatch(transactionActions.clearAllTransaction());
      }
    }
  }, [data, action]);
  return {
    addTransaction: (transactionData) => {
      const url = "/transaction";
      optionObject.method = "POST";
      optionObject.body = JSON.stringify(transactionData);
      setReqData({ url, optionObject, auth: true,message:'Adding Transaction...' });
      setAction("addtransaction");
    },
    editTransaction: (transactionData) => {
      const url = `/transaction?id=${transactionData.id}`;
      optionObject.method = "PATCH";
      optionObject.body = JSON.stringify(transactionData);
      setReqData({ url, optionObject, auth: true,message:'Editing Transaction...' });
      setAction("edittransaction");
    },
    deleteTransaction: (transactionData) => {
      const url = `/transaction?id=${transactionData.transactionId}`;
      optionObject.method = "DELETE";
      setReqData({ url, optionObject, auth: true ,message:'Deleting Transaction...'});
      setAction("deletetransaction");
    },
    deleteAllTransaction: () => {
      const url = "/transaction/deleteall";
      optionObject.method = "DELETE";
      setReqData({ url, optionObject, auth: true ,message:'Clearing All Tranaction...'});
      setAction("deletealltransaction");
    },
  };
};

export default transactionActionsToDatabase;
