import React from "react";
import classes from "./Button.module.css";
const Button = ({ buttonText, isEnabled,handleClick,styleClasses}) => {
  let classList = `${classes.button} ${styleClasses}`
  return (
    <div>
      {isEnabled ? (
        <button className={classList} onClick={handleClick}>{buttonText}</button>
      ) : (
        <button className={classes.button} disabled>
          {buttonText}
        </button>
      )}
    </div>
  );
};

export default Button;
