import React, { useState } from "react";
import Button from "./Button";
import IncomeOrExpenseItem from "./IncomeOrExpenseItem";
import classes from "./IncomeOrExpenseListContainer.module.css";
const IncomeOrExpenseListContainer = ({ list, title }) => {
  const [sort, setSort] = useState(0);
  let sortedList;
  if (!sort) {
    sortedList = list.sort((a, b) => new Date(a.date) - new Date(b.date));
  }
  if (sort == 1) {
    sortedList = list.sort((a, b) => new Date(b.date) - new Date(a.date));
  }
  if (sort == 2) {
    sortedList = list.sort((a, b) => a.expenseType - b.expenseType);
  }
  let prevDate = null;
  const calcDate = (date) => {
    let currentDate = new Date();
    let currDateString = currentDate.toJSON().slice(0, 10);
    if (date == currDateString) {
      if (prevDate != date) {
        prevDate = date;
        return "Today";
      } else {
        return null;
      }
    } else if (prevDate == date) {
      return null;
    } else {
      prevDate = date;
    }
    return date;
  };

  const filterButtonClickHandler = () => {
    if (title == "Expenses" && sort == 1) {
      return setSort(2);
    }
    setSort(!sort);
  };

  return (
    <div className={classes.income__expense__list__container}>
      <div className={classes.heading}>
        <div>
          <h4>All {title}</h4>
        </div>
        <div>
          <span>Total {list.length}</span>
        </div>
      </div>

      <div className={classes.filter__btn__container}>
        <Button
          buttonText={sort == 2 ? "Sorted By Types" : "Sorted By Dates"}
          isEnabled={true}
          styleClasses={classes.filter__btn}
          handleClick={filterButtonClickHandler}
        ></Button>
      </div>

      {/* all items */}
      <div>
        {sortedList.length == 0 && (
          <p className={classes.nothing__show__msg}>Nothing to show !</p>
        )}
        {sortedList.map((item) => {
          return (
            <div key={item.id}>
              <span className={classes.showdate}>{calcDate(item.date)}</span>
              <IncomeOrExpenseItem
                id={item.id}
                title={item.title}
                description={item.description}
                amount={item.amount}
                expenseType={item.expenseType}
              ></IncomeOrExpenseItem>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default IncomeOrExpenseListContainer;
