import React from "react";
import classes from "./Header.module.css";
import { NavLink } from "react-router-dom";
import homeIcon from "../img/home.png";
import settingIcon from "../img/settings.png";
import addTransactionIcon from "../img/addTransaction.png";
import visualizeIcon from "../img/chart.png";
import { useSelector } from "react-redux";
const Header = () => {
  const userData = useSelector((state) => state.user);
  return (
    <div className={classes.header__container}>
      <div className={classes.heading__container}>
        <h3>Hello, {userData.user ? userData.user.userName : "User"}</h3>
      </div>
      <div className={classes.nav__links__container}>
        <ul>
          <li>
            <NavLink
              to="/home"
              className={(navdata) => (navdata.isActive ? classes.active : "")}
            >
              <img src={homeIcon}></img>
              <span>Home</span>
            </NavLink>
          </li>
          <li>
            <NavLink
              to="/addtransaction"
              className={(navdata) => (navdata.isActive ? classes.active : "")}
            >
              <img src={addTransactionIcon}></img>
              <span>Add Transaction</span>
            </NavLink>
          </li>
          <li>
            <NavLink
              to="/visualize"
              className={(navdata) => (navdata.isActive ? classes.active : "")}
            >
              <img src={visualizeIcon}></img>
              <span>Visualize</span>
            </NavLink>
          </li>
          <li>
            <NavLink
              to="/setting"
              className={(navdata) => (navdata.isActive ? classes.active : "")}
            >
              <img src={settingIcon}></img>
              <span>Setting</span>
            </NavLink>
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Header;
