import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Route, Routes } from "react-router-dom";
import "./App.css";
import CheckUserExist from "./Auth/CheckUserExist";
import PageNoTFound from "./components/PageNoTFound";
import ShowMassageModal from "./components/ShowMassageModal";
import useSendHttpRequest from "./custom-Hooks-Helper-Function/useSendHttpRequest";
import AddTransaction from "./Pages/AddTransactionPage/AddTransaction";
import EditTransaction from "./Pages/EditTransaction/EditTransaction";
import Home from "./Pages/HomePage/Home";
import LoginOrSignup from "./Pages/LoginOrSignupPage/LoginOrSignup";
import Setting from "./Pages/SettingPage/Setting";
import TransactionDetail from "./Pages/TransactionDetail/TransactionDetail";
import Visualize from "./Pages/Visualize/Visualize";
import Welcome from "./Pages/WelcomePage/Welcome";
import { transactionActions } from "./store/transactionSlice";

function App() {
  const [reqData, setReqData] = useState({
    url: null,
    optionObject: null,
    auth: null,
  });
  const dispatch = useDispatch();
  const data = useSendHttpRequest(reqData);
  const userData = useSelector((state) => state.user);
  useEffect(() => {
    if (userData.token) {
      const url = "/transaction";
      const optionObject = {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      };
      setReqData({ url, optionObject, auth: true ,message:'Fetching data...'});
    }
  }, [userData.user.email]);

  useEffect(() => {
    if (data) {
      data.transactionList.map((transaction) => {
        transaction.id = transaction._id.toString();
        delete transaction._id;
        dispatch(transactionActions.addNewTransaction(transaction));
      });
    }
  }, [data]);

  return (
    <div className="App">
      {<ShowMassageModal></ShowMassageModal>}
      <Routes>
        <Route
          path="/"
          element={
            <CheckUserExist urlPath="/">
              <Welcome></Welcome>
            </CheckUserExist>
          }
        ></Route>
        <Route
          path="/LoginOrSignup"
          element={
            <CheckUserExist urlPath="/loginorsignup">
              <LoginOrSignup></LoginOrSignup>
            </CheckUserExist>
          }
        ></Route>
        <Route
          path="/home"
          element={
            <CheckUserExist>
              <Home></Home>
            </CheckUserExist>
          }
        ></Route>
        <Route
          path="/addtransaction"
          element={
            <CheckUserExist>
              <AddTransaction></AddTransaction>
            </CheckUserExist>
          }
        ></Route>
        <Route
          path="/transactiondetails"
          element={
            <CheckUserExist>
              <TransactionDetail></TransactionDetail>
            </CheckUserExist>
          }
        ></Route>
        <Route
          path="/edittransaction"
          element={
            <CheckUserExist>
              <EditTransaction></EditTransaction>
            </CheckUserExist>
          }
        ></Route>
        <Route
          path="/setting"
          element={
            <CheckUserExist>
              <Setting></Setting>
            </CheckUserExist>
          }
        ></Route>
        <Route
          path="/visualize"
          element={
            <CheckUserExist>
              <Visualize></Visualize>
            </CheckUserExist>
          }
        ></Route>
        <Route path="/*" element={<PageNoTFound></PageNoTFound>}></Route>
      </Routes>
    </div>
  );
}

export default App;
