import React  from "react";
import { useSelector } from "react-redux";
import { Link, useLocation } from "react-router-dom";
import classes from "./TransactionDetail.module.css";
import Button from "../../components/Button";
import transactionActionsToDatabase from "../../store/TransactionActions";
const TransactionDetail = (props) => {
  const tranActions = transactionActionsToDatabase()
  const { transactionList } = useSelector((state) => state.transaction);
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const id = params.get("id");
  const transactionArr = transactionList.filter(
    (transaction) => transaction.id == id
  );
  
  const { title, transactionType, date, description, amount } =
    transactionArr[0]? transactionArr[0] : {};

  const transactionTypeText =
    transactionType == "income" ? "Income" : `Expense (${title})`;

  const deleteTransactionHandler = () => {
    tranActions.deleteTransaction({ transactionId: id })
  };

  return (
    <>
      <div className={classes.transaction__details__container}>
        <div className={classes.row__1}>
          <h3>Transaction</h3>
          <span>{date}</span>
        </div>
        <div className={classes.row__2}>
          <h3>{transactionTypeText}</h3>
          <span>${amount}</span>
          <p
            className={
              transactionType == "income" ? classes.income__description : null
            }
          >
            {description}
          </p>
        </div>
        <div className={classes.row__3}>
          <Button
            buttonText="Delete"
            isEnabled={true}
            styleClasses={classes.btn__1}
            handleClick={deleteTransactionHandler}
          ></Button>
          <Link to={`/edittransaction?id=${id}`}>
            <Button
              buttonText="Edit"
              isEnabled={true}
              styleClasses={classes.btn__2}
            ></Button>
          </Link>
        </div>
      </div>
    </>
  );
};

export default TransactionDetail;
