import React from "react";
import { NavLink } from "react-router-dom";
import rightArrowImg from "../../img/right-arrow.png";
import classes from "./Welcome.module.css";
const Welcome = () => {
  return (
    <div className={classes.welcome__page__container}>
      <h2>Welcome To Expense Tracker App</h2>
      <div className={classes.animation__container}>
        <div className={classes.animation__text__container}>
          <div>Track</div>
          <div>Manage</div>
          <div>Visualize</div>
          <div>Track</div>
        </div>
        <div>Your Expenses</div>
      </div>
      <div className={classes.button__container}>
        <NavLink to='/LoginOrSignup'>
          <button>
            Enter
            <div className={classes.img__container}>
              <img src={rightArrowImg}></img>
            </div>
          </button>
        </NavLink>
      </div>
    </div>
  );
};

export default Welcome;
