import React from 'react'
import classes from './ExpenseType.module.css'
const ExpenseType = ({imageSrc,text,setSelectedExpenseType,id,currSelected}) => {
    const handleClick = ()=>{
      setSelectedExpenseType(id+1)
    }
    let calssList = `${classes.expensetype__container}`;
    if(id+1==currSelected){
        calssList=`${classes.expensetype__container} ${classes.active}`
    }
  return (
    <div className={calssList} onClick={handleClick}>
        <img src={imageSrc}></img>
        <span>{text}</span>
    </div>
  )
}

export default ExpenseType