import React, { useEffect, useState } from 'react'
import AddTransactionForm from '../../components/AddTransactionForm'
import transactionActionsToDatabase from '../../store/TransactionActions'
const AddTransaction = () => {
  const [formData,setFormData] = useState(null);
  const tranActions = transactionActionsToDatabase()
  useEffect(()=>{
    if(formData){
       tranActions.addTransaction(formData)
    }
  },[formData]);
  return (
    <div>
        <AddTransactionForm submitFormData={setFormData}></AddTransactionForm>
    </div>
  )
}

export default AddTransaction