import React from "react";
import classes from './IncomeOrExpenseItem.module.css';
import incomeIcon from '../img/income-icon.png'
import expenseTypeData from "../custom-Hooks-Helper-Function/ExpenseTypeData";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
const IncomeOrExpenseItem = ({title,description,amount,expenseType,id}) => {
  const {userSettings} = useSelector(state=>state.user);
  let currencySymbol = userSettings.currency== 'rupee' ? '' : '$';
    const expenseTypeArr = expenseTypeData() ;
    let imageSrc = incomeIcon;
    if(expenseType){
         imageSrc = expenseTypeArr[expenseType-1].imageSrc
    }
    const navigate = useNavigate()
    const handleClick = ()=>{
       navigate(`/transactiondetails?id=${id}`)
    }
  return (
    <div onClick={handleClick}>
      <div className={classes.income__expense__item}>
        <div className={classes.income__item__left}>
          <div className={classes.income__item__left__icon}>
            <img src={imageSrc}></img>
          </div>
          <div>
            <h3>{title}</h3>
            <p>{description}</p>
          </div>
        </div>
        <div className={classes.income__item__right}>
          <h3>{currencySymbol}{amount}</h3>
        </div>
      </div>
    </div>
  );
};

export default IncomeOrExpenseItem