import React, { useEffect, useState } from "react";
import userAvatar from "../../img/avatar.png";
import classes from "./Setting.module.css";
import facebookIcon from "../../img/facebook.png";
import youtubeIcon from "../../img/youtube.png";
import instagramIcon from "../../img/instagram.png";
import Button from "../../components/Button";
import editIcon from "../../img/edit.png";
import { useDispatch, useSelector } from "react-redux";
import { userActions } from "../../store/userSlice";
import transactionActionsToDatabase from "../../store/TransactionActions";
import userActionsToDatabase from "../../store/UserActions";
import Input from "../../components/Input";
const Setting = () => {
  const { userSettings, user } = useSelector((state) => state.user);
  const [currency, setCurrency] = useState(userSettings.currency);
  const [image, setImage] = useState(null);
  const [userName, setUserName] = useState(user.userName);
  const [editing, setEditing] = useState(false);

  const userActionsDatabase = userActionsToDatabase();
  const tranActions = transactionActionsToDatabase();
  const dispatch = useDispatch();

  // if username changes then also update it on local state.
  useEffect(() => {
    if (user.userName) {
      setUserName(user.userName);
    }
  }, [user.userName]);
  // if no image is set then use the default avatar but if image is available then convert the buffer image to base64;
  let base64String;
  let imgSrc = userAvatar;
  if (userSettings.image) {
    base64String = btoa(
      String.fromCharCode(...new Uint8Array(userSettings.image.data))
    );
    imgSrc = `data:image/png;base64,${base64String}`;
  }

  // if userSettings.currency change then update the state of currency.
  // when the page is reloaded at first rendered the initial value of the currency is the userSettings.currency one. but when the userSetting is changed from getting the data from localstorage then currency does not change because useState(userSettings.currency) this value is initial value means when component is mounted then the value is used.the initial value does not change even if the userSetting change.
  useEffect(() => {
    setCurrency(userSettings.currency);
  }, [userSettings.currency]);

  // bases on which currency is selected style is applied.
  let dollarclass = null;
  let rupeeclass = null;
  if (currency == "dollar") {
    dollarclass = classes.selected;
  } else {
    rupeeclass = classes.selected;
  }

  // change currency type
  const currencyHandler = () => {
    setCurrency((prev) => (prev == "dollar" ? "rupee" : "dollar"));
  };

  // whenever user select a image just alert the user that in order to see the updated image update setting.
  useEffect(() => {
    if (image) {
      alert("Update setting to see the changes.");
    }
  }, [image]);

  const imageChangeHandler = (e) => {
    if (e.target.files[0].size > 51200) {
      return alert("Please select image less than 50kb");
    }
    setImage(e.target.files[0]);
  };

  const updateSettingHandler = () => {
    if (userName.length < 3) {
      return alert("Username must between 3 to 6 characters !");
    } else if (userName.length > 6) {
      return alert("Username must between 3 to 6 characters !");
    }
    const formData = new FormData();
    formData.append("image", image);
    formData.append("currency", currency);
    formData.append("name", userName);
    userActionsDatabase.updateSetting(formData);
  };

  const clearAllTransaction = () => {
    tranActions.deleteAllTransaction();
  };

  const logoutAppHandler = () => {
    // clear the data from loalstorage then reload the page which will automatically redirect to welcome page.
    dispatch(userActions.clearUserData());
    window.location.reload();
  };

  const handleEditUserDetails = () => {
    setEditing(!editing);
  };

  const changeUserNameHandler = (e) => {
    setUserName(e.target.value);
  };

  return (
    <>
      <div className={classes.settingpage__container}>
        <h3>Settings</h3>
        {/* user details */}
        <div className={classes.user__details}>
          <div className={classes.user__details__left}>
            <div className={classes.user__avatar}>
              <div className={classes.input__container}>
                <input
                  type="file"
                  accept="image/*"
                  placeholder="ddd"
                  onChange={imageChangeHandler}
                ></input>
              </div>
              <div className={classes.img__container}>
                <img src={imgSrc}></img>
              </div>
              <span>Select Avatar</span>
            </div>
            <div>
              {editing && (
                <Input
                  changeHandler={changeUserNameHandler}
                  defaultValue={userName}
                  isFocused={true}
                ></Input>
              )}
              {!editing && <p>{userName}</p>}
              <p>{user.email}</p>
            </div>
          </div>
          <div
            className={classes.edit__img__container}
            onClick={handleEditUserDetails}
          >
            <img src={editIcon}></img>
          </div>
        </div>
        {/* user settings */}
        <div className={classes.user__settings}>
          {/* row 1 for currency */}
          <div className={`${classes.row__1} ${classes.row}`}>
            <p>Your Preferred Currency</p>
            <div className={classes.select__currency} onClick={currencyHandler}>
              <span className={dollarclass}>$</span>
              <span className={rupeeclass}></span>
            </div>
          </div>
          {/* row 2 for language */}
          <div className={`${classes.row__2} ${classes.row}`}>
            <p>Your Preferred Language</p>
            <span>English</span>
          </div>
          {/* row 3 for scolial media links */}
          <div className={`${classes.row__3} ${classes.row}`}>
            <p>Connect With Us:-</p>
            <div>
              <a href="https://www.facebook.com/" target="__blank">
                <img src={facebookIcon}></img>
              </a>
              <a href="https://www.youtube.com/" target="__blank">
                <img src={youtubeIcon}></img>
              </a>
              <a href="https://www.instagram.com/" target="__blank">
                <img src={instagramIcon}></img>
              </a>
            </div>
          </div>
          {/* row 4 for clear transactions */}
          <div className={`${classes.row__4} ${classes.row}`}>
            <p>App Actions</p>
            <div>
              <Button
                buttonText="Clear All Transactions"
                isEnabled={true}
                handleClick={clearAllTransaction}
              ></Button>
            </div>
          </div>
          {/* row 4 for upadate or logout */}
          <div className={`${classes.row__5} ${classes.row}`}>
            <Button
              buttonText="Logout App"
              isEnabled={true}
              styleClasses={classes.btn__1}
              handleClick={logoutAppHandler}
            ></Button>
            <Button
              buttonText="Update Setting"
              isEnabled={true}
              styleClasses={classes.btn__2}
              handleClick={updateSettingHandler}
            ></Button>
          </div>
          {/* app details */}
          <div className={classes.app__details}>
            <p>App version 1.1.0</p>
            <p> 2020-2022 Expense Tracker Inc.</p>
          </div>
        </div>
      </div>
    </>
  );
};

export default Setting;
